(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .controller('Policy1Controller', Policy1Controller);

  /** @ngInject */
  function Policy1Controller($http, PolicyService, $stateParams,$state) {
    var vm = this

    vm.text1 = ''
    vm.text2 = 'John Doe'
    vm.policyDetails = {}

    vm.policies = [{
      name: '',
      number: '51000306',
      type: '',
      amount: '8,320',
      color: 'green',
      img: 'assets/images/green-bicycle.png'
    },{
      name: '',
      number: '. 51007779',
      type: '',
      amount: '3,000',
      color: 'skyblue',
      img: '/assets/images/l-blue-bag.png'
    },{
      name: '',
      number: '. 51002303',
      type: '',
      amount: '4,770',
      color: 'blue',
      img: '/assets/images/d-blue-car.png'
    }];

    vm.btn1 = '';
    vm.btn2 = '';

    var init = function() {    
      var req = {
        method: 'GET',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + $stateParams.policyNumber
      };
      $http(req)
        .then(function (response) {
          vm.policyDetails = response.data
          PolicyService.setPolicyDetails(vm.policyDetails)
          PolicyService.setPolNo($stateParams.policyNumber)
          vm.policies[0].number = '. ' + vm.policyDetails.Cont.ContID
          vm.policies[0].type = PolicyService.mapPlanName(vm.policyDetails.Cont.Risks["1"].RiskBody.Plan)
          vm.policies[0].amount = vm.policyDetails.Cont.Risks["1"].RiskBody.TotalPremium + ''
          vm.text2 = vm.policyDetails.Cont.Owner.Name

        }, function () {
          $state.go('policy-login');
      });
    }

    init();

  }
})();

