    vm.relationships = [{
      value: 'self',
      display: ''
    },{
      value: 'relative',
      display: ''
    }];

    vm.text2 = '';
    vm.placeholder1 = '';

    vm.relationshipTypes = [{
      value: 'spouse',
      display: ''
    },{
      value: 'child',
      display: ''
    },{
      value: 'grand-child',
      display: ''
    },{
      value: 'parent',
      display: ''
    },{
      value: 'grandparent',
      display: ''
    }];

    vm.text3 = '';
    vm.placeholder2 = '';

    vm.text4 = '';
    vm.placeholder3 = ' /  / ';

    vm.calendarFormat = 'yyyy / MM / dd';

    vm.datepickerOptions = {
      showWeeks: false
    };

